import React, { Component } from "react";
import { BrowserRouter } from "react-router-dom";
import "./style/css/App.css";
import axios from "axios";

// Component imports
import Header from "./Components/Header";
import Panel from "./Components/Panel";
import MapViewer from "./Components/MapViewer";
import Graph from "./Components/Graph";
import NewGraph from "./Components/newGraph";
import Footer from "./Components/Footer";

class App extends Component {
  constructor() {
    super();
    this.state = {
      countries: [],
      graphData: {
        cases: [],
      },
      searchCountry: "",
    };
  }

  componentDidMount() {

    let fetchCountries = "https://corona.lmao.ninja/v2/countries?yesterday&sort";
    let fetchGraphdata =
      "https://corona.lmao.ninja/v2/historical/all";

    const requestOne = axios.get(fetchCountries);
    const requestTwo = axios.get(fetchGraphdata);

    axios
      .all([requestOne, requestTwo])
      .then(
        axios.spread((...responses) => {
          const responseOne = responses[0];
          const responseTwo = responses[1];
          this.setState({
            countries: responseOne.data,
            graphData: responseTwo.data,
          });
        })
      )
      .catch((errors) => {
        console.log(errors);
      })
      .finally(() => {console.log(this.state.graphData)});
  }

    handleInput = (e) => {
    this.setState({ searchCountry: e.target.value });

  };

  render() {
    console.log("hi");

    // Enables searchbar
    let filteredCountries = this.state.countries.filter((country) => {
      return country.country
        .toLowerCase()
        .includes(this.state.searchCountry.toLowerCase());
    });

    return (
      <BrowserRouter>
        <Header />
        <div className="app">
          <div id="tiles" className="tile is-ancestor">
            <div id="search-tool" className="tile is-4 is-vertical is-parent">
              <article
                id="panel-tile"
                className="tile is-child box"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 1 }}
              >
                <article id="search-panel" className="panel is-black">
                  <p className="panel-heading">Search</p>
                  <Panel
                    handleInput={this.handleInput}
                    data={filteredCountries}
                  />
                </article>
              </article>
            </div>
            <div className="tile is-parent is-vertical">
              <article
                className="mapviewer tile is-child box"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 1 }}
              >
                <MapViewer data={this.state.countries} />
              </article>

              <article
                id="graph-container"
                className="tile is-child box"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 1 }}
              >
                <Graph graphData={this.state.graphData} />
              </article>
            </div>
          </div>
          <Footer />
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
