import React, { useState } from "react";
import {
  Bar,
  BarChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  ResponsiveContainer,
  LineChart,
  Tooltip,
} from "recharts";

const Graph = (props) => {
  //Set category for graph
  const [category, setCategory] = useState("activeCases");

  //Generate graph data
  let graphData = props.graphData;
  let data = [];
  for (var i = 0; i < 30; i++) {
    data.push({
      date: Object.keys(graphData.cases)[i],
      cases: Object.values(graphData.cases)[i],
    });
  }

  return (
    <div id="graph-block">
      <article className="panel is-black">
        <p className="panel-heading">Graph</p>
        <p className="panel-tabs">
          <a
            className={category === "activeCases" ? "is-active" : ""}
            onClick={() => setCategory("activeCases")}
          >
            Confirmed
          </a>
          <a
            className={category === "recovered" ? "is-active" : ""}
            onClick={() => setCategory("recovered")}
          >
            Cured
          </a>
          <a
            className={category === "deaths" ? "is-active" : ""}
            onClick={() => setCategory("deaths")}
          >
            Deaths
          </a>
        </p>
        {/* <div className="panel-block">
          <p className="control has-icons-left">
            <input className="input is-dark is-small" type="text" placeholder="Search" />
            <span className="icon is-left">
              <i className="fas fa-search" aria-hidden="true"></i>
            </span>
          </p>
          
        </div>
        <div className="panel-block">
          
        </div> */}
        <div className="panel-block">
          <ResponsiveContainer width="100%" aspect={2.1}>
            <LineChart className="graph" data={data}>
              <Line dataKey="cases" stroke="rgb(56, 123, 247)" />
              <XAxis dataKey="date" />
              <YAxis />
              <Tooltip />
            </LineChart>
          </ResponsiveContainer>
        </div>
      </article>
    </div>
  );
};

export default Graph;
